import { _decorator, Component, director, Label, Node, Root } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ProfileInfo')
export class ProfileInfo extends Component {
    @property({ type: Label })
    public fpsLabel: Label | null = null;

    @property({ type: Label })
    public drawcallscLabel: Label | null = null;

    @property({ type: Label })
    public frameTimeLabel: Label | null = null;

    @property({ type: Label })
    public venderLabel: Label | null = null;

    start() {
    
    }

    update(deltaTime: number) {
        this.fpsLabel!.string = `FPS: ${director.root.fps}`;
        this.venderLabel!.string = `Vender: ${director.root.device.vendor}`;
        this.drawcallscLabel!.string = `DrawCalls: ${director.root.device.numDrawCalls}`;
        this.frameTimeLabel!.string = `FrameTime: ${(director.root.frameTime * 1000).toFixed(2)} ms`;
    }
}

